.app {
  width: 640px;
  margin: 0 auto;
}
.logo {
  width: 280px;
  margin: 0 auto;
  display: block;
}
.genre__list {
  margin: 0;
  padding: 0;
  width: 200px;
  border: 1px solid #000000;
  border-radius: 8px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.genre__item {
  list-style: none;
  text-align: center;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: #de5e43;
  font-size: 24px;
  padding: 3px 0;
}

.genre__item:hover {
  background-color: #dedede;
}

.genre__item-text {
  cursor: pointer;
}

.active {
  color: #000000;
  text-decoration: underline;
}

.more {
  display: block;
  text-align: center;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: #de5e43;
  font-size: 15px;
  padding: 3px 0;
  cursor: pointer;
}
.gif {
  width: 60px;
  height: auto;
  margin-left: 10px;
}
main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.joke {
  padding: 0 10px;
  text-align: center;
  margin: 15px 0px;
  font-size: 24px;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.joke-btn {
  margin-top: 45px;
  color: #de5e43;
  border: 1px solid #000000;
  border-radius: 8px;
  background-color: transparent;
  padding: 8px 20px;
  cursor: pointer;
  transition: background-color .2s;
  outline: none;
  font-size: 18px;
}

.joke-btn:hover {
  background-color: #de5e43;
  color: #ffffff;
}
@keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

@-webkit-keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

.lds-css {
  position: relative;
}

.lds-double-ring {
  position: relative;
}

.lds-double-ring div {
  position: absolute;
  width: 160px;
  height: 160px;
  top: 20px;
  left: 20px;
  border-radius: 50%;
  border: 8px solid #000;
  border-color: #de5e43 transparent #de5e43 transparent;
  -webkit-animation: lds-double-ring 1.4s linear infinite;
  animation: lds-double-ring 1.4s linear infinite;
}

.lds-double-ring div:nth-child(2) {
  width: 140px;
  height: 140px;
  top: 30px;
  left: 30px;
  border-color: transparent #ffcb75 transparent #ffcb75;
  -webkit-animation: lds-double-ring_reverse 1.4s linear infinite;
  animation: lds-double-ring_reverse 1.4s linear infinite;
}

.lds-double-ring {
  width: 77px !important;
  height: 77px !important;
  -webkit-transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
  transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
}
